import React from "react";
import { Card, CardBody, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label} from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import  { Link } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import Loader from '../../Loader/Loader';


import "../../../assets/css/font-awesome.min.css";
import "react-form-builder2/dist/app.css";
import "./AssignInspection.css";
class AssignInspection extends React.Component {
  constructor(props){
    super(props);
    this.state = {      
      subCategoryList: [],      
      loading: false,      
      categoryList: [], 
      formField: { categoryId: '', subCategoryId: '', inspection_name: '', organizationId: '', employeeId: '', templateId: '', storeId: ''},
      formErrors: {category: '', subcategory: '', organization: '', employee: '', template: '', store:'', inspection_name: '', error: ''},
      organizationList: [],
      employeeList: [],
      templateList: [],
      templateData: [],
      storeList: [],
      inspectionId: "",     
      employeeIds: [],
      formValid: true,
      templatePreviewData : [],
      formProccessing: false,
      changeDropDown: false
    }     
    
    this.getEmployeeList = this.getEmployeeList.bind(this);
    this.getTemplateList = this.getTemplateList.bind(this);   
    this.submitHandler = this.submitHandler.bind(this);  
    this.resetForm = this.resetForm.bind(this); 
  }

  // Fetch the subCategory List
  componentDidMount() {
    const { match: { params } } = this.props;
    if(params.inspectionId !== undefined && params.inspectionId !=="") {
      this.setState({inspectionId: params.inspectionId});
      this.getInspectionDetail(params.inspectionId);
    }
      
    this.categoryList();
    this.getEmployeeList();
  }

  getInspectionDetail(inspectionId) {
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('inspection/'+inspectionId)
        .then( res => {
          console.log(res);
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);  
            this.props.history.push('/admin/manage-research');  
            return;
          } 
          const inspectionDetail = res.data.data;
          let formField = this.state.formField;
          formField.categoryId = inspectionDetail.categoryId;
          formField.inspection_name = inspectionDetail.inspectionName;
          formField.employeeId = inspectionDetail.employeeId;
          formField.templateId = inspectionDetail.templateId;
          this.getTemplateList(inspectionDetail.categoryId, false);
          this.setState({loading:false, formField: formField, inspectionId: inspectionDetail.inspectionId, employeeIds: [inspectionDetail.employeeId]});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } ) 
  }
  /*categoryList List API*/
  categoryList() {   
   
    commonService.getAPIWithAccessToken('template/category')
      .then( res => {
        console.log(res);
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   

        this.setState({categoryList: res.data.data});     
       
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else           
          toast.error(err.message);    
        
      } )
    
  }
  
  

  /*get Employee List*/
  getEmployeeList(hideEmployee = true) {
    const formField = this.state.formField;
    this.setState( { loading: true}, () => { 
      commonService.getAPIWithAccessToken('profile/list?role=both')
      .then( res => {
        console.log(res);
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   
       // let selectedEmployeList = this.state.selectedEmployeList;
        if(hideEmployee){
          formField.employeeId = '';
          
        }
        
        this.setState({employeeList: res.data.data.profileList,  formField: formField, loading: false});     
        
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else { 
          this.setState( {  loading: false } );        
          toast.error(err.message); 

        }
      } )
    })

  }

  

  /*Get Template List*/
  getTemplateList(categoryId, hideSubcat = true){
    const formField = this.state.formField;
    if(categoryId === "") {      
      formField.templateId = '';
      this.setState({ formField: formField, templateList: [] });
      return;
    }
    this.setState( { loading: true}, () => { 
      //let organizationIdQuery = "";
      commonService.getAPIWithAccessToken("template?categoryId="+categoryId)
      .then( res => {
        console.log(res);
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   
        if(hideSubcat)
          formField.templateId = '';
        this.setState({templateList: res.data.data, formField: formField, loading: false});     
        
      } )
      .catch( err => {         
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else { 
          this.setState( {  loading: false } );        
          toast.error(err.message); 

        }
      } )
    })
  }
  /* Input Field On changes*/
  changeHandler = event => {    
    const name = event.target.name;
    const value = event.target.value;
    const formField = this.state.formField
    formField[name] = value;
    this.setState({ formField: formField });
  };
  /* Change Employee handler*/
  changeEmployeeHandler = event => { 
    let employeeIds = [];
    event.target.selectedOptions.forEach(function(data) {
      employeeIds.push(data.value); 
    });
    
    this.setState({employeeIds: employeeIds});
   
  }
  
  /*Handle catgeory Input Change and bind subcategory*/
  changeCategoryHandle = event => {
    const name = event.target.name;
    const value = event.target.value;
    const formField = this.state.formField
    formField[name] = value;
    this.setState({ formField: formField });
    this.getTemplateList(formField.categoryId, value);
  }

  
  
  /* Validate Form */
  validateForm() {
    
    let formErrors = {};

    const formField = this.state.formField;
    let formValid = true;
    if(formField.categoryId === undefined || formField.categoryId === "") {
      formErrors['categoryId'] = "Please setect category";
      formValid = false;
    }
    if(formField.templateId === undefined || formField.templateId === "") {
      formErrors['templateId'] = "Please setect template";
      formValid = false;
    }
    if(formField.inspection_name === undefined || formField.inspection_name === "") {
      formErrors['inspection_name'] = "Please enter clinical trials name";
      formValid = false;
    }
    if(this.state.inspectionId === "" && this.state.employeeIds.length === 0){
      formErrors['employee'] = "Please select employee";
      formValid = false;
    }
    this.setState({formErrors: formErrors});
    return formValid;

  }
  /* Set Error Class*/
  errorClass(error) {
    return(error.length === 0 ? '' : 'has-error');
  }

  /* Submit Form Handler*/
  submitHandler (event) {
    event.preventDefault();   
    event.target.className += " was-validated";
    if(!this.validateForm())
      return false;
    this.setState( { loading: true}, () => {
      
      const formInputField = this.state.formField;
      let formData = {
        "categoryId": formInputField.categoryId, 
        "inspectionName": formInputField.inspection_name, 
        "templateId": formInputField.templateId,
        "assignEmployee": this.state.employeeIds
      };
      
      if(this.state.inspectionId !== ""){
        formData.employeeId = this.state.employeeIds[0];
      }
     
      if(this.state.inspectionId !== "" ) {
        formData.inspectionId = this.state.inspectionId;
        commonService.putAPIWithAccessToken('inspection', formData)
        .then( res => {        
           
          if ( undefined === res.data.data || !res.data.status ) { 
            this.setState( { loading: false} );
            toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false});
          toast.success(res.data.message);
          this.props.history.push('/admin/manage-inspection/inspection');
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { loading: false } );
            toast.error(err.message);
        } )
      }
      else {
        commonService.postAPIWithAccessToken('inspection', formData)
        .then( res => {        
           
          if ( undefined === res.data.data || !res.data.status ) { 
            this.setState( { loading: false} );
            toast.error(res.data.message);
            return;
          } 
          
          this.setState({ modal: false});
          toast.success(res.data.message);
          this.props.history.push('/admin/manage-research/research');
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else
            this.setState( { loading: false } );
            toast.error(err.message);
        } )
      }
      
    } );
    
  };

  resetForm(){
    this.props.history.push('/admin/manage-research/assign-research');
  }

  
  render() {
    const {  loading, categoryList, employeeList, templateList,  formErrors } = this.state;     
    let loaderElement ='';
    
    if(loading)
      loaderElement = <Loader />
    const isMulti = (this.state.inspectionId !== "") ? false : true;
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card>
              <CardHeader className="mainHeading">
                <strong>Clinical Trials</strong>
              </CardHeader>
              <CardBody>
                {loaderElement}
                <ToastContainer />
                <Form onSubmit={this.submitHandler} noValidate>
                  
                  <Row>
                    
                    <Col lg={12}>
                        <FormGroup>
                          <Label htmlFor="inspection_name">Clinical Trials Name</Label>            
                          <Input type="text" placeholder="Clinical Trials Name *" invalid={formErrors['inspection_name'] !== undefined && formErrors['inspection_name'] !== ""} value={this.state.formField.inspection_name} onChange={this.changeHandler} id="inspection_name" name="inspection_name" required />
                          <div className="invalid-feedback">
                            {formErrors['inspection_name']}
                          </div>
                        </FormGroup>
                    </Col>
                    
                    <Col lg={6}>
                      <FormGroup> 
                        <Label htmlFor="categoryId">Category <span className="mandatory">*</span></Label>            
                        <Input type="select" placeholder="category Name *" id="categoryId" invalid={formErrors['categoryId'] !== undefined && formErrors['categoryId'] !== ""} name="categoryId" value={this.state.formField.categoryId} onChange={this.changeCategoryHandle} required >
                          <option value="">Select Category</option>
                          {categoryList.map((categoryItem, index) =>
                            <SetCategoryDropDownItem key={index} categoryItem={categoryItem} selectedCategory={this.state.formField.categoryId} />
                          )}
                        </Input>
                        <div className="invalid-feedback">
                            {formErrors['categoryId']}
                          </div>
                      </FormGroup>
                    </Col>
                    
                    <Col lg={6}>
                      <FormGroup> 
                        <Label htmlFor="templateId">Template <span className="mandatory">*</span></Label>            
                        <Input type="select" placeholder="Subcategory Name *" id="templateId" invalid={formErrors['templateId'] !== undefined && formErrors['templateId'] !== ""} name="templateId" value={this.state.formField.templateId} onChange={this.changeHandler} required >
                          <option value="">Select Template</option>
                          {templateList.map((templateItem, index) =>
                            <SetTemplateDropDownItem key={index} templateItem={templateItem} selectedCategory={this.state.formField.templateId} />
                          )}
                        </Input>
                        <div className="invalid-feedback">
                            {formErrors['templateId']}
                        </div>
                      </FormGroup>
                      {this.state.formField.templateId ? <Col lg={5} md={5}><div className="previewTemplateIcon"><Link to={`/common/template/${this.state.formField.templateId}`} target="_blank" className="Preview-btn">Preview Template </Link></div></Col>  : ""}
                    </Col>
                   
                    
                    <Col md={6}>                     
                        
                      <FormGroup>
                        <Label htmlFor="employee">Employee</Label>
                         <Input type="select" multiple= {isMulti} invalid={formErrors['employee'] !== undefined && formErrors['employee'] !== ""}  name={`employeeId`} value={this.state.employeeIds} onChange={this.changeEmployeeHandler} required >
                          <option value="">Select Employee</option>
                          {employeeList.map((employeeItem, employeeIndex) =>
                            <SetEmployeeDropDownItem key={employeeIndex} employeeItem={employeeItem}  />
                          )}
                        </Input>
                        <div className="invalid-feedback">
                            {formErrors['employee']}
                        </div>
                      </FormGroup>                    
                                               
                    </Col>
                    <Col md={12}></Col>
                    <Button color="primary" disabled={!this.state.formValid} type="submit">Save</Button>
                    <Button color="secondary" onClick={this.resetForm}>Cancel</Button>
                  </Row>
                </Form>
                
              </CardBody>
            </Card>
          </Col>  
        </Row>
      </div>
    );
  }
}

function SetCategoryDropDownItem (props) {
  const categoryItem = props.categoryItem;
  return (<option value={categoryItem.categoryId} >{categoryItem.categoryName}</option>)
}


function SetTemplateDropDownItem(props){
  const templateDetail = props.templateItem;
  return (<option value={templateDetail.templateId} >{templateDetail.templateName}</option>)
}

function SetEmployeeDropDownItem(props){
  const employeeDetail = props.employeeItem;
  return (<option value={employeeDetail.authId} >{employeeDetail.firstName} {employeeDetail.lastName}</option>)
}

export default AssignInspection;
